import React, { useEffect, useState, memo } from "react";
import { Table, Button, Space, Divider } from "antd";
import DatePicker from "components/DatePicker";

const { RangePicker } = DatePicker;

function Index(props) {
  const {
    fetchApi,
    columns,
    forceUpdate,
    data,
    rangeSearch,
    components,
    current,
    total,
    pageSize,
    showTotal,
    rowSelection,
    scrollX = false,
    DividerShow = true,
  } = props;
  const defaultRangeTime = rangeSearch
    ? rangeSearch.rangeTime
      ? [
          rangeSearch.rangeTime[0].format(
            rangeSearch.layout || "YYYY-MM-DD HH:mm:ss"
          ),
          rangeSearch.rangeTime[1].format(
            rangeSearch.layout || "YYYY-MM-DD HH:mm:ss"
          ),
        ]
      : null
    : null;

  const [loading, setLoading] = useState(false);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: pageSize || 20,
    total: total || 100000,
    showTotal: showTotal,
  });
  const [filters, setFilters] = useState({});
  const [sorter, setSorter] = useState([]);
  const [rangeTime, setRangeTime] = useState(defaultRangeTime);
  const [disabled, setDisabled] = useState(!rangeTime);
  const [dates, setDates] = useState(
    rangeSearch ? rangeSearch.rangeTime || [] : null
  );

  const dataSource = data?.map((user, index) => {
    return {
      ...user,
      key: index + 1,
    };
  });

  const fetchData = async (pagination, filters, sorter) => {
    setLoading(true);

    let params = {
      page: pagination.current,
      pageSize: pagination.pageSize,
      filters: filters,
    };

    if (rangeTime) {
      params.startTime = rangeTime[0];
      params.endTime = rangeTime[1];
    }

    if (sorter.length > 0) {
      params.sorter = sorter[0];
    }
    // console.log("rangeTime", rangeTime, params)

    await fetchApi(params);
    setLoading(false);
  };

  useEffect(() => {
    setPagination({ ...pagination, total: total ?? 10000 });
  }, [total /*, pageSize*/]);

  useEffect(() => {
    fetchData(pagination, filters, sorter);

    if (current) {
      current(pagination);
    }
  }, [
    /*pagination, */ filters,
    forceUpdate,
    sorter,
    pageSize,
    pagination.current,
  ]);

  const handleTableChange = (pagination, filters, sorter) => {
    console.log("handleTableChange:", pagination, filters, sorter);
    setPagination(pagination);
    setFilters(filters);
    if (sorter.order) {
      setSorter([
        {
          key: sorter.columnKey,
          order: sorter.order === "ascend" ? "asc" : "desc",
        },
      ]);
    } else {
      setSorter([]);
    }
  };

  const rangeChange = (dates, dateStrings) => {
    setRangeTime(dateStrings);
    setDisabled(!dates);
  };

  const disabledDate = (current) => {
    if (
      !dates ||
      dates.length === 0 ||
      !rangeSearch ||
      !rangeSearch.rangeDays
    ) {
      return false;
    }
    // if (rangeSearch.rangeTime) {
    //   console.log("current", current)
    // }

    const tooLate =
      dates[0] && current.diff(dates[0], "days") >= rangeSearch.rangeDays;
    const tooEarly =
      dates[1] && dates[1].diff(current, "days") >= rangeSearch.rangeDays;
    return tooEarly || tooLate;
  };

  return (
    <>
      {/*<Space direction='vertical' size='middle'>*/}
      {rangeSearch && (
        <Space>
          <RangePicker
            disabledDate={disabledDate}
            defaultValue={rangeSearch.rangeTime || null}
            format={rangeSearch.layout || "YYYY-MM-DD HH:mm:ss"}
            showTime={rangeSearch.showTime || false}
            onChange={rangeChange}
            onCalendarChange={(value) => setDates(value)}
          />
          <Button
            type="primary"
            onClick={() => fetchData(pagination, filters, sorter)}
            disabled={disabled}
          >
            Inquire
          </Button>
        </Space>
      )}
      {DividerShow && <Divider dashed />}
      <Table
        // rowKey={key}
        rowSelection={rowSelection}
        components={components}
        scroll={{ x: scrollX }}
        bordered
        loading={loading}
        pagination={pagination}
        columns={columns}
        dataSource={dataSource}
        onChange={handleTableChange}
      />
      {/*</Space>*/}
    </>
  );
}
export default memo(Index, (prev, next) => {
  // console.log("prev=", prev, "next=", next);
  return (
    JSON.stringify(prev.data) === JSON.stringify(next.data) &&
    JSON.stringify(prev.columns) === JSON.stringify(next.columns) &&
    prev.forceUpdate == next.forceUpdate
  );
});
