import React, { useState } from "react";
import Page from "components/Page";
import ListPage from "../../components/ListPage";
import dayjs from "utils/utc";
import axios from "utils/axios";
import { Link } from "react-router-dom";
import { getColumnSearchProps } from "utils/columnSearch";
import { useSelector } from "react-redux";

function Index(props) {
  // 初始化数据
  const [recordData, setRecord] = useState([]);
  const [total, setTotal] = useState(30);
  const appidList = useSelector((state) => state.appids);

  const [appidObj, setAppidObj] = useState({});
  const [appidListFilter, setAppidListFilter] = useState([]);
  const getAppids = (appid) => {
    for (const element of appidList) {
      appidObj[element.appid] = element.appid;
      appidListFilter.push({ text: element.appid, value: element.appid });
    }
    return "";
  };

  // 获取服务器数据，设置数据
  const fetchData = async (params) => {
    let data = await axios.get("/user/loginLogList", { params });
    if (data && data.code === 0) {
      setTotal(data.data.total | 0);
      setRecord(data.data.listdata);
    }
  };
  // 设置数据表  //...getColumnSearchProps('uid') ,
  const columns = [
    {
      title: "UID",
      dataIndex: "uid",
      ...getColumnSearchProps("uid"),
      render: (uid) => <Link to={`/user/userInfo?uid=${uid}`}>{uid}</Link>,
    },
    {
      title: "Appid",
      dataIndex: "appid",
      render: (value) => {
        const m = appidObj;
        return m[value] || 0;
      },
      filters: appidListFilter,
    },
    {
      title: "Time",
      dataIndex: "time",
      render: (value) =>
        dayjs.unix(value).utc().utcOffset(330).format("YYYY-MM-DD HH:mm:ss"),
    },
    { title: "IP", dataIndex: "ip" },
    { title: "Version", dataIndex: "version" },
    { title: "Deviceid", dataIndex: "deviceid" },
    {
      title: "Sysversion",
      dataIndex: "sysversion",
      render: (value) => {
        const m = {
          pc: "pc",
          "5.1.1": "5.1.1",
        };
        return m[value] || "pc";
      },
      filters: [
        { text: "PC", value: "pc" },
        { text: "5.1", value: "5.1.1" },
      ],
    },
    { title: "Devname", dataIndex: "devname" },
  ];

  return (
    <Page title="用户登录列表">
      <ListPage
        // rangeSearch={{layout: 'YYYY-MM-DD',time: [dayjs(), dayjs()], rangeDays: 7}}
        rangeSearch={{
          showTime: true,
          rangeDays: 30,
          rangeTime: [dayjs().subtract(7, "day"), dayjs()],
        }}
        columns={columns}
        fetchApi={fetchData}
        data={recordData}
        total={total}
      />
    </Page>
  );
}

export default Index;
